<template>
  <div class="panoramicAnalysis">
    <div class="top">
      <div class="content-left content">
        <div class="content-left-top">
          <div class="text">潜在过户数</div>
          <div class="value">{{ potentialTransferNumber?potentialTransferNumber:0 }}</div>
        </div>
        <div class="content-left-bottom">
          <div class="text">疑似租赁数</div>
          <div class="value">{{ suspectedLeaseNumber?suspectedLeaseNumber:0 }}</div>
        </div>
      </div>
      <div class="content-right content content-border">
        <div class="content-title">
          <div class="content-title-left">
            <span class="block"></span>
            <span class="text">主题标签</span>
          </div>
        </div>
        <div class="chart-main content-table">
          <themeLabelList
            :labelData="labelData"
            :pageConfig="labelPageConfig"
            @changePage="changePage"
            :showDetailData="labelDetailData"
            @clickDetail="getLabelDetailData"
            @changeDetailPage="changePage"
            @exportExcel="themeLabelExportExcel"
            ref="themeLabelList"
          ></themeLabelList>
        </div>
      </div>
    </div>
    <div class="center">
      <div class="content content-border">
        <div class="content-title">
          <div class="content-title-left">
            <span class="block"></span>
            <span style="min-width: 100px;">潜在过户与疑似租赁分布</span>
          </div>
          <div class="content-title-right">
            <el-select
              style="height: 30px; margin-right: 20px"
              v-model="potentialSuspectedForm.municipalBureau"
              size="mini"
              clearable
              placeholder="请选择供电局"
              @change="getPotentialSuspectedData"
            >
              <el-option
                v-for="item in municipalBureauOptions"
                :key="item.value"
                :label="item.name"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
        </div>
        <div class="chart-main">
          <barCharts
            v-if="potentialSuspectedRes"
            :barChartsData="potentialSuspectedData"
            :name="'潜在过户与疑似租赁分布'"
            @setOption="setOption"
            :legendStatus="true"
            :legendPosition="'right'"
            :isMultiple="true"
            :isShadow="true"
            :barWidth="20"
            :barType="'bar'"
            :maxShowNumber="9"
          ></barCharts>
        </div>
      </div>
      <div class="content content-border">
        <div class="content-title">
          <div class="content-title-left">
            <span class="block"></span>

            <div class="change-type">
              <span
                class="change-button"
                :class="potentialMarkersActive === '1' ? 'active' : ''"
                @click="changePotentialMarkers('1')"
              >潜在过户标记</span>
              <span
                class="change-button"
                :class="potentialMarkersActive === '2' ? 'active' : ''"
                @click="changePotentialMarkers('2')"
              >疑似房屋租赁</span>
            </div>
          </div>
          <div class="content-title-right">
            <el-select
              style="height: 30px; margin-right: 20px"
              v-model="potentialMarkersQueryForm.municipalBureau"
              size="mini"
              clearable
              placeholder="请选择供电局"
              @change="getListAndGraphData"
            >
              <el-option
                v-for="item in municipalBureauOptions"
                :key="item.value"
                :label="item.name"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
        </div>
        <div class="chart-main content-table">
          <bdaTable
            class="main-table"
            :tableData="potentialMarkersData"
            :headerColor="headerColor"
            :isPage="true"
            :pageConfig="potentialMarkersPageConfig"
            @changePage="changePage"
          ></bdaTable>
        </div>
      </div>
    </div>
    <div class="bottom">
      <div class="content content-border">
        <div class="content-title">
          <div class="content-title-left">
            <span class="block"></span>
            <span>用户提醒与预警</span>
          </div>
        </div>
        <div class="chart-main">
          <barCharts
            v-if="dailySeatRes"
            :barChartsData="dailySeatData"
            :legendStatus="true"
            :name="'用户提醒与预警'"
            :legendPosition="'right'"
            :isMultiple="true"
            :isShadow="false"
            :isLabel="false"
            :barWidth="40"
            :barType="'stack'"
            :maxShowNumber="11"
            @setOption="setOption"
          ></barCharts>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from '@/base/mixin/potential-suspected';
import bdaTable from "@/components/bda-table/bda-table";
import barCharts from "@/components/bar-charts/bar-charts";
import themeLabelList from "@/components/theme-label-list/theme-label-list";
import { exportExcelGet } from "@/utils/export";
import {CP_PATH} from "@/config"
import axios from "axios";
export default {
  components: {
    bdaTable,
    barCharts,
    themeLabelList
  },
  name: "panoramicAnalysis",
  mixins: [Interface],
  data() {
    return {
      //潜在过户数
      potentialTransferNumber: 0,
      //疑似租赁数
      suspectedLeaseNumber: 0,
      //潜在过户与疑似租赁分布
      potentialSuspectedForm: {
        municipalBureau: ""
      },
      //地市局选项
      municipalBureauOptions: [],
      //关注标签
      labelData: [],

      labelQueryForm: {
        menuType: "4",
        current: 1,
        size: 10
      },
      //分页设置
      labelPageConfig: {
        name: "label",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //标签详情
      labelDetailData: {},
      labelDetailQueryForm: {
        dbcolumnAS: "",
        menuType: "4",
        tagName: "",
        current: 1,
        size: 10
      },
      //分页设置
      labelDetailPageConfig: {
        name: "labelDetail",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //潜在过户与疑似租赁分布
      potentialSuspectedData: {},
      potentialSuspectedRes: false,
      //潜在过户标记
      potentialMarkersActive: "1",
      potentialMarkersData: {},
      potentialMarkersQueryForm: {
        municipalBureau: "",
        current: 1,
        size: 10
      },
      //分页设置
      potentialMarkersPageConfig: {
        name: "potentialMarkers",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //每日坐席
      dailySeatData: {},
      dailySeatRes: false,
      //表头颜色
      headerColor: "",
      //图表组件数据
      setOptionData: {}
    };
  },
  mounted() {
    this.getLabelData()
    this.getTotlNumber()
    this.getMunicipalBureauOptions()
    this.getPotentialSuspectedData();
    this.getListAndGraphData();
    window.addEventListener("resize", () => {
      if (this.setOptionData["潜在过户与疑似租赁分布"])
        this.setOptionData["潜在过户与疑似租赁分布"].resize();
      if (this.setOptionData["用户提醒与预警"])
        this.setOptionData["用户提醒与预警"].resize();
    });
  },
  methods: {
    //图表组件
    setOption(name, val) {
      this.setOptionData[name] = val;
    },
    //改变分页
    changePage(data) {
      console.log(data, "data");
      if (data.name === "label") {
        this.labelQueryForm.size = data.size;
        this.labelQueryForm.current = data.current;
        this.getLabelData();
      }
      if (data.name === "labelDetail") {
        this.labelDetailQueryForm.dbcolumnAS = data.keyValue || "";
        this.labelDetailQueryForm.size = data.size;
        this.labelDetailQueryForm.current = data.current;
        this.getLabelDetailData();
      }
      if (data.name === "potentialMarkers") {
        this.potentialMarkersQueryForm.size = data.size;
        this.potentialMarkersQueryForm.current = data.current;
        this.getPotentialMarkersData();
      }
    },
    //主题标签导出
    themeLabelExportExcel2(datas) {
      try {
        exportExcelGet(
            "/leanSeatingSkillController/exportLabelUserDisplay" +
            "?tagId=" +
            datas.tagId
        );
      }finally {
        this.$refs.themeLabelList.changeLoading()
      }
    },
    //下面这个方法备用
    themeLabelExportExcel(datas) {
      this.excelLoading = true
      let path = '/leanSeatingSkillController/exportLabelUserDisplay'
      let data = {
        tagId: datas.tagId,
      }
      axios({
        method: "get",
        url: CP_PATH + path,
        params:data,
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        },
        responseType: 'blob'
      }).then(res => {
        const content = res.data
        const blob = new Blob([content])
        const fileName = '导出信息.xls'
        if ('download' in document.createElement('a')) { // 非IE下载
          const elink = document.createElement('a')
          elink.download = fileName
          elink.style.display = 'none'
          elink.href = URL.createObjectURL(blob)
          document.body.appendChild(elink)
          elink.click()
          URL.revokeObjectURL(elink.href) // 释放URL 对象
          document.body.removeChild(elink)
        } else { // IE10+下载
          navigator.msSaveBlob(blob, fileName)
        }
      }).finally(()=>{
        this.$refs.themeLabelList.changeLoading()
      }).catch(()=>{
        this.$refs.themeLabelList.changeLoading()
      })
    },
    // 获取关注标签
    getLabelData() {
      this.themeLabelDisplay(this.labelQueryForm).then(res => {
        this.labelPageConfig.total = res.total;
        this.labelPageConfig.size = this.labelQueryForm.size;
        this.labelPageConfig.current = this.labelQueryForm.current;
        this.labelData = res.list.map(e => {
          return {
            ...e,
            name: e.tagName,
            text: e.tagDescription,
            value: e.account || 0
          };
        });

        console.log(this.labelData, "labelData");
      });
    },
    //获取标签详情信息
    getLabelDetailData(data) {
      if (data) {
        this.labelDetailQueryForm.tagId = data.tagId;
        this.labelDetailQueryForm.size = 10;
        this.labelDetailQueryForm.current = 1;
      }
      let columnNameData = [];
      this.labelUserDisplay({ ...this.labelDetailQueryForm }).then(res => {
        this.labelDetailPageConfig.total = res.result[0]
          ? res.result[0].total
          : 0;
        this.labelDetailPageConfig.size = this.labelDetailQueryForm.size;
        this.labelDetailPageConfig.current = this.labelDetailQueryForm.current;
        if (res.showField[this.labelDetailQueryForm.tagId]) {
          columnNameData = res.showField[this.labelDetailQueryForm.tagId]
            .map(e => {
              return {
                prop: e.dbcolumnAS,
                label: e.dbcolumnTitle
              };
            })
            .filter(
              e => e.label !== "地区编码" && e.label !== "县区局编码" && e.label !== "供电单位编码" && e.label !== "供电电网编码"
            )
            .filter(
              e => e.label !== "地区编码" && e.label !== "县区局编码" && e.label !== "供电单位编码" && e.label !== "供电电网编码"
            );
        }
        console.log(columnNameData, "columnNameData");
        res.result.shift();
        this.labelDetailData = {
          tableData: [...res.result],
          columnNameData: columnNameData,
          name: "labelDetail",
          headerColor: "#1899fa",
          title: "客户明细列表",
          total: this.labelDetailPageConfig.total,
          current: this.labelDetailQueryForm.current,
          size: this.labelDetailQueryForm.size
        };
      });
    },
    //获取潜在过户总数与疑似租赁总数
    getTotlNumber() {
      this.selectTotal({}).then(res => {
        this.potentialTransferNumber = res.qzghs
        this.suspectedLeaseNumber = res.yszls
      })
    },
    // 获取潜在过户与疑似租赁分布
    getPotentialSuspectedData(val) {
      let orgLev = ''
      this.municipalBureauOptions.forEach(item=>{
        if (item.value===this.potentialSuspectedForm.municipalBureau){
          orgLev = item.orgLev
        }
      })
      this.potentialSuspectedRes = false;
      this.selectPotentialTransfer(val ? {
        "powerSupplyCode":this.potentialSuspectedForm.municipalBureau,
        "orgLev":orgLev
      } : {}).then(res => {
        let dataMap1 = []
        let dataMap2 = []
        let dataMap3 = []
        let dataMap4 = []
        let xNameData = []
        res.forEach(e => {
          xNameData.push(e.areaName)
          dataMap1.push(e.potentialHouseholds)
          dataMap2.push(e.confirmPotentialHouseholds)
          dataMap3.push(e.handleTransfer)
          dataMap4.push(e.suspectedLease)
        });
        this.potentialSuspectedData = {
          dataMap: {
            '潜在过户数': dataMap1,
            '确认潜在过户数': dataMap2,
            '办理过户数': dataMap3,
            '疑似租赁数': dataMap4
          },
          xNameData: xNameData,
          colorMap: {
            潜在过户数: [
              // '#55BFFF', '#2790FF'
              ["#2790FF", "#55BFFF", "#ddf2ff"]
            ],
            确认潜在过户数: [
              // '#55BFFF', '#2790FF'
              ["#19A7CD", "#66D6FF", "#def6fe"]
            ],
            办理过户数: [
              // '#55BFFF', '#2790FF'
              ["#28B16E", "#6AE3AA", "#def5fd"]
            ],
            疑似租赁数: [
              // '#55BFFF', '#2790FF'
              ["#E69622", "#F8CC67", "#fdf3df"]
            ]
          },
          xyName: {
            x轴: { name: "", color: "#4D4D4D", diffcolor: false },
            y轴: [{ name: "单位：户数", color: "#808080" }]
          }
        };
        this.potentialSuspectedRes = true;

      })

    },
    //切换潜在过户与疑似租赁
    changePotentialMarkers(val) {
      this.potentialMarkersActive = val;
      this.potentialMarkersQueryForm.municipalBureau = '';
      this.getListAndGraphData()
    },
    //获取潜在过户与疑似租赁图表数据
    getListAndGraphData() {
      this.potentialMarkersQueryForm.current = 1
      this.getPotentialMarkersData();
      this.getDailySeatData()
    },
    // 获取潜在过户标记
    getPotentialMarkersData() {
      this.headerColor = "#1899fa";
      let columnNameData = [
        { prop: "yhbh", label: "用户编码" },
        { prop: "yhmc", label: "用户名称" },
        { prop: "yddz", label: "用电地址" },
        { prop: "gdj", label: "供电局" },
        { prop: "gddw", label: "供电单位" },
        { prop: "tqmc", label: "台区" },
        { prop: "xlmc", label: "线路线段" },
        { prop: "ysd", label: "疑似度" },
        { prop: "sfzqbj", label: "确认是否正确标记" },
        { prop: "sfblgh", label: this.potentialMarkersActive === '1' ? "标记后是否办理过户" : "标记后是否修改档案信息" }
      ];
      this.potentialMarkersData = {};
      // if (this.potentialMarkersQueryForm.municipalBureau) {
      //   this.potentialMarkersQueryForm.size = 10
      //   this.potentialMarkersQueryForm.current = 1
      // }
      this.potentialTransferMark({
        mark: this.potentialMarkersActive,
        city: this.potentialMarkersQueryForm.municipalBureau,
        size: this.potentialMarkersQueryForm.size,
        current: this.potentialMarkersQueryForm.current
      }).then(res => {
        this.potentialMarkersPageConfig.total = res.total;
        this.potentialMarkersPageConfig.size = this.potentialMarkersQueryForm.size;
        this.potentialMarkersPageConfig.current = this.potentialMarkersQueryForm.current;
        res.records.forEach(e => {
          e.ysd = e.ysd + '%'
        })
        this.potentialMarkersData = {
          tableData: [...res.records],
          columnNameData: columnNameData
        };
      });
    },
    //用户提醒与预警
    getDailySeatData() {
      //   if(this.potentialMarkersActive==='1'){}
      //   if(this.potentialMarkersActive==='2'){}
      this.dailySeatRes = false;
      this.userReminderAndWarning({
        mark: this.potentialMarkersActive,
        city: this.potentialMarkersQueryForm.municipalBureau,
      }).then(res => {
        let xNameData = []
        let dataMap1 = []
        let dataMap2 = []
        let dataMap3 = []
        res.forEach(e => {
          xNameData.push(e.name)
          dataMap1.push(e.correct)
          dataMap2.push(e.undetermined)
          dataMap3.push(e.error)
        })
        this.dailySeatData = {
          dataMap: {
            '正确标记': dataMap1,
            '待定标记': dataMap2,
            '错误标记': dataMap3,
          },
          xNameData: xNameData,
          colorMap: {
            正确标记: [["#2790FF", "#55BFFF"]],
            待定标记: [["#28B16E", "#6AE3AA"]],
            错误标记: [["#E69622", "#F8CC67"]]
          },
          xyName: {
            x轴: { name: "", color: "#808080", diffcolor: false },
            y轴: [{ name: "客户数", color: "#808080" }]
          }
        };
        this.dailySeatRes = true;
      })

    },
    // 获取地市局数据
    getMunicipalBureauOptions() {
      this.cityDownFrameLevel({}).then(res => {
        this.municipalBureauOptions = res.map(e => {
          return {
            name: e.orgName,
            value: e.orgCode,
            orgLev:e.orgLev
          }
        })
      })
    }
  }
};
</script>

<style scoped lang="scss">
::v-deep .el-table .warning-row {
  // background: #e7f6f7;
  background: #e9f3fe;
}
::v-deep .el-pagination__total {
  margin-left: 10px;
}
.panoramicAnalysis {
  width: 100%;
  height: 100%;
  font-size: 14px;
  box-sizing: border-box;
  $Color: linear-gradient(270deg, #17a8ff 0%, #1a84f4 100%);
  $bottomColor: linear-gradient(270deg, #38c7b1 0%, #11a5b0 100%);
  .top,
  .center,
  .bottom {
    width: 100%;
    // height: calc(50% - 10px);
    box-sizing: border-box;
    .content-border {
      background: #ffffff;
      border: 2px solid rgba(26, 132, 244, 0.2);
      border-radius: 4px;
    }
    .content-title {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 30px;
      line-height: 30px;
      padding: 5px;
      font-weight: bold;
      color: #1a84f4;
      .block {
        display: inline-block;
        width: 8px;
        height: 16px;
        margin: 0 10px 0 0;
        background-image: $Color;
      }
      .content-title-left {
        display: flex;
        align-items: center;
        margin-left: 10px;
      }
      .content-title-right {
        display: flex;
        align-items: center;
        margin-right: 10px;
      }
    }
    .chart-main {
      width: 100%;
      height: calc(100% - 40px);
    }
    .content {
      width: 100%;
      height: 100%;
      display: flex;
      flex-wrap: wrap;
      padding: 10px;
      box-sizing: border-box;
    }
  }
  .top {
    height: 300px;
    display: flex;
    .content-left {
      width: calc(15% - 10px);
      height: 100%;
      margin-right: 20px;
      display: flex;
      flex-direction: column;
      .content-left-top,
      .content-left-bottom {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: calc(50% - 10px);
        margin-bottom: 20px;
        padding: 20px;
        box-sizing: border-box;
        background-image: linear-gradient(135deg, #2790ff 0%, #55bfff 100%);
        border-radius: 4px;
        .text {
          width: 100%;
          height: 50%;
          font-size: 16px;
          color: #ffffff;
          font-weight: bold;
        }
        .value {
          width: 100%;
          height: 50%;
          font-size: 24px;
          color: #ffffff;
          font-weight: bold;
          display: flex;
          flex-direction: column-reverse;
          align-items: flex-end;
        }
      }
      .content-left-bottom {
        margin-bottom: 0px;
        background-image: linear-gradient(135deg, #28b16e 0%, #6ae3aa 100%);
      }
    }
    .content-right {
      width: calc(85% - 10px);
      height: 100%;
    }
  }
  .center {
    margin-top: 20px;
    height: 800px;
    text-align: center;
    .content {
      height: calc(50% - 20px);
      margin-bottom: 20px;
      .content-title-left {
        .change-type {
          width: 100%;
          height: 30px;
          display: flex;
          align-items: center;
          .change-button {
            display: inline-block;
            width: 100px;
            height: 30px;
            text-align: center;
            font-size: 14px;
            color: #808080;
            cursor: pointer;
            &:hover {
              color: #1a84f4;
              // border-bottom: 1px solid #1a84f4;
            }
          }
          .active {
            color: #1a84f4;
            border-bottom: 1px solid #1a84f4;
          }
        }
      }
      .text {
        width: 80px;
        color: #4d4d4d;
      }
    }
    .chart-main {
      .main-table {
        margin-top: 10px;
        height: 100%;
      }
      .main-pagination {
        padding: 10px 0;
      }
    }
  }
  .bottom {
    margin-top: 20px;
    height: 300px;
  }
}
</style>
