/**
 *  潜在过户与疑似租赁 mixin module
 *  @module base/mixin/potentialSuspected
 *  @author coder
 */
import {
  themeLabelDisplay,
  labelUserDisplay,
  selectTotal,
  cityDownFrameLevel,
  selectPotentialTransfer,
  potentialTransferMark,
  userReminderAndWarning
} from '@/base/api/potential-suspected'

// mixin
export default {

  /**
   * 潜在过户与疑似租赁数据存储载体对象
   * @member potentialSuspected
   * @property {Array} [list=[]] 获取潜在过户与疑似租赁列表响应数据
   * @property {number} [page=null] 获取潜在过户与疑似租赁列表响应页码
   * @property {number} [limit=null] 获取潜在过户与疑似租赁列表页大小，每页记录条数
   * @property {number} [total=0] 获取潜在过户与疑似租赁列表数据记录总条数
   * @property {object} [model=null] 获取潜在过户与疑似租赁单条记录
   */
  data() {
    return {
      potentialSuspected: {
        list: [],
        page: null,
        limit: null,
        total: 0,
        model: null
      }
    }
  },
  methods: {

    /**
     * 主题标签-标签展示
     * @function themeLabelDisplay
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    themeLabelDisplay(data) {
      return themeLabelDisplay(data).then(res => {
        return res
      })
    },
    /**
     * 主题标签-通过标签查询覆盖用户信息
     * @function labelUserDisplay
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    labelUserDisplay(data) {
      return labelUserDisplay(data).then(res => {
        return res
      })
    },
    /**
     * 潜在过户总数与疑似租赁总数
     * @function selectTotal
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    selectTotal(data) {
      return selectTotal(data).then(res => {
        return res
      })
    },
    /**
     * 供电局下拉框
     * @function cityDownFrameLevel
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    cityDownFrameLevel(data) {
      return cityDownFrameLevel(data).then(res => {
        return res
      })
    },
    /**
     * 潜在过户与疑似租赁分布
     * @function selectPotentialTransfer
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    selectPotentialTransfer(data) {
      return selectPotentialTransfer(data).then(res => {
        return res
      })
    },
    /**
     * 潜在过户标记列表
     * @function potentialTransferMark
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    potentialTransferMark(data) {
      return potentialTransferMark(data).then(res => {
        return res
      })
    },
    /**
     * 展示区间内的各类标记数
     * @function userReminderAndWarning
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    userReminderAndWarning(data) {
      return userReminderAndWarning(data).then(res => {
        return res
      })
    }
  }
}
